  outline: none;
  padding: 10px 20px;
  font-size: 17px;
  font-weight: 600;
}

input::-ms-input-placeholder {
  color: #000;
  font-weight: 600;
}

input::placeholder {
  color: #000;
  font-weight: 600;
}

button[type="submit"] {
  cursor: pointer;
  fill: #4056b4;
  background-color: rgba(0, 0, 0, 0);
